.intro {
  border: solid blue 3px;
}

.intro-titulo {
  color: green;
}

.intro-carrusel-imagen {
  width: 200px;
  height: 450px;
  object-fit: cover;
  /* filter: invert(0.4) sepia(1) hue-rotate(20deg); */
}

.intro-carrusel-contenido-imagen {
  width: 300px;
  object-fit: contain;
}

.carousel-caption {
  background-color: aqua !important;
  max-width: 600px;
}

.intro-contenedor {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #fff;
  color: #000000;
  padding: 20px 60px;
}

.intro-contenedor-informacion {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-width: 300px;
  width: 50%;
  height: 500px;
}

.intro-contenedor-imagen {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro-contenedor-imagen-img {
  width: 300px;
}
.intro-carousel .carousel-control-prev {
  /* position: absolute; */
  max-width: 50px;
  /* top: 0; */
}

.intro-carousel .carousel-control-next {
  /* position: absolute; */
  max-width: 50px;

}

@media (max-width: 850px) {
  .intro-contenedor-imagen,
  .intro-carousel .carousel-control-prev,
  .intro-carousel .carousel-control-next {
    display: none;
  }

}
